import http from "../http-common";
import authHeader from "services/auth-header";

const getAll = (params, thisCollection) => {
  return http.get("/"+thisCollection,  {headers: authHeader(), params});
};

const getCount = (params, thisCollection) => {
	console.log(thisCollection)
	console.log(params)
return http.get("/"+thisCollection+"/count", {headers: authHeader(), params});
};

const get = (id, thisCollection) => {
  return http.get("/"+thisCollection+"/${id}", {headers: authHeader()});
};

const create = (data, thisCollection) => {
  return http.post("/"+thisCollection, data, {headers: authHeader()});
};

const update = (id, data, thisCollection) => {
  return http.put("/"+thisCollection+"/"+id, data, {headers: authHeader()});
};

const remove = (id, thisCollection) => {
  return http.delete("/"+thisCollection+"/"+id, {headers: authHeader()});
};

const removeAll = (thisCollection) => {
  return http.delete("/"+thisCollection, {headers: authHeader()});
};

const findByTitle = (title, thisCollection) => {
  return http.get("/"+thisCollection+"?filter[where][title][like]=${title}", {headers: authHeader()});
};

export default {
  getAll,
  getCount,
  get,
  create,
  update,
  remove,
  removeAll,
  findByTitle
};
