import React, { useState, useEffect } from "react";
import TutorialDataService from "../services/TutorialService";
import { Link } from "react-router-dom";
import {Avatar, Button, Container, Dialog, DialogTitle, 
  DialogContent, DialogContentText, DialogActions, 
  Grid, Typography, TableContainer, Paper, Popover, Table, 
  TableHead, TableRow, TableCell, TableBody, TextField } from "@material-ui/core";
import MenuBookIcon from '@material-ui/icons/MenuBook';
import Pagination from "@material-ui/lab/Pagination";
import { makeStyles, useTheme } from '@material-ui/core/styles';
import { red, blue, yellow} from '@material-ui/core/colors'
import { useTranslation } from "react-i18next";
import 'translations/i18n.js';
import ShowNest from 'crud/ShowNest'
import ShowPopover from 'crud/ShowPopover'
import collection from 'crud/collections'

const useStyles = makeStyles((theme) => ({
  popover: {
    pointerEvents: 'none',
  },
  container: {
    paddingTop: theme.spacing(2),
    paddingBottom: theme.spacing(2)
  },
  paper: {
    marginTop: theme.spacing(2), 
    padding: theme.spacing(2),
    display: "flex",
    overflow: "auto",
    flexDirection: "column",
	alignItems: 'center'
  },
  
  avatar: {
    margin: theme.spacing(1),
    backgroundColor: theme.palette.secondary.main,
  },
  submit: {
    margin: theme.spacing(3, 0, 2),
  },
    table: {
    minWidth: 650,
  },
}));

const TutorialsList = ({searchTitle, page, setPage, thisCollection}) => {
  const { t } = useTranslation();
  const getAge = birthDate => Math.floor((new Date() - new Date(birthDate).getTime()) / 3.15576e+10)
  const classes = useStyles()
  const theme = useTheme()
  const [tutorials, setTutorials] = useState([]);
  const [currentTutorial, setCurrentTutorial] = useState(null);
  const [currentIndex, setCurrentIndex] = useState(-1);
 
  const [open, setOpen] = React.useState(false);
  const [isEdit, setIsEdit] = React.useState(false);
  
  const [anchorEl, setAnchorEl] = React.useState(null);

  const handlePopoverOpen = (event) => {
    setAnchorEl(event.currentTarget);
	setCurrentTutorial(tutorials[event.currentTarget.id]);
    setCurrentIndex(event.currentTarget.id);
    console.log(event.currentTarget.id)
	console.log(event.currentTarget.key)
  };

  const handlePopoverClose = () => {
    setAnchorEl(null);
  };

  const popOpen = Boolean(anchorEl);

  const [count, setCount] = useState(0);
  
  const [pageSize, setPageSize] = useState(5);

  const pageSizes = [5, 10, 15];
   
  const getRequestParams = (searchTitle, page, pageSize, isCount) => {
    let params = {"filter":{"where":{}}};
	  let countParams = {"where":{}};
    if (searchTitle) {
      params["filter"]["where"]["$text"] = {"search":searchTitle}; 
	  countParams["where"]["$text"] = {"search":searchTitle};
    }

    if (page) {
      params["filter"]["skip"] = (page - 1)*pageSize;
    }

    if (pageSize) {
      params["filter"]["limit"] = pageSize;
    }

    return isCount?countParams:params;
  };

  const retrieveTutorials = () => {
	const countParams = getRequestParams(searchTitle, page, pageSize, true);
    const params = getRequestParams(searchTitle, page, pageSize, false);


    TutorialDataService.getCount(countParams, thisCollection)
      .then((response) => {
		let trueCount = response.data.count/pageSize
		let pageCount = trueCount===parseInt(trueCount)?trueCount:parseInt(trueCount)+1

        setCount(pageCount);

      })
      .catch((e) => {
        console.log(e);
      });
	
    TutorialDataService.getAll(params, thisCollection)
      .then((response) => {
        setTutorials(response.data);
        console.log(response.data);
      })
      .catch((e) => {
        console.log(e);
      });
  };

  useEffect(retrieveTutorials, [searchTitle, page, pageSize]);

  const refreshList = () => {
    retrieveTutorials();
    setCurrentTutorial(null);
    setCurrentIndex(-1);
  };

  const setActiveTutorial = (row, index) => {
    setCurrentTutorial(row);
    setCurrentIndex(index);
    setOpen(true)
  };

  const handlePageChange = (event, value) => {
    setPage(value);
  };

  const handlePageSizeChange = (event) => {
    setPageSize(event.target.value);
    setPage(1);
  };

  const handleClickOpen = () => {
    setOpen(true);
  };

  const handleEdit = () => {
    setIsEdit(true);
  };

  const handleClose = () => {
    setOpen(false);
    setIsEdit(false);
  };

  return (
  <Grid container
	direction="row"
	justify="flex-start"
	alignItems="center"
	>
    <Paper className={classes.paper}>      
	   <Grid
			container
			spacing={1}
			justify="center"
			alignItems="center"
		>
	     <Avatar style={{width: theme.spacing(7), height: theme.spacing(7), backgroundColor: blue[100] }}>
		<Typography variant="h4" style={{color: blue[800]}}>
		  {collection[thisCollection].icon&&collection[thisCollection].icon}
		  </Typography>
        </Avatar>

		</Grid>
		<Grid container direction="column" spacing={2} alignItems="center" justify="flex-end">
      <Table styles={{minWidth: 650}}>
      <TableHead>
        <TableRow>
			<TableCell align="center" size="medium" >{t("Avatar")}</TableCell>
			{Object.keys(collection[thisCollection].field).map((theField, index)=>{
			  let dataField=collection[thisCollection].field[theField]
			  if (dataField.table===true){
				return (
				<TableCell align="center" size="medium"  key={theField+index} >{t(dataField.show)}</TableCell>
				)
			  }
			})}
      </TableRow>
      </TableHead>
      <TableBody>
		{tutorials.map((row, index) => (
        <TableRow hover onClick={() => setActiveTutorial(row, index)} 
			onMouseEnter={handlePopoverOpen}
			onMouseLeave={handlePopoverClose}
			id={index}
			row={row}
			key={index}>

		 <TableCell> 
		 {row['birthday']?
			<Avatar>
			{ getAge(row['birthday'])<=15 &&(row["sex"]==='1'?"\u{1f466}":"\u{1f467}")}
			{ (getAge(row['birthday']) >15 && getAge(row['birthday']) <=60 )&&( row["sex"]==='1'?"\u{1f9d1}":"\u{1f469}")}
			{ (getAge(row['birthday']) >60 ) && (row["sex"]==='1'?"\u{1f474}":"\u{1f475}")}
			</Avatar>
			:
			<Avatar style={{ backgroundColor: blue[index*100] }}>
				{index}
			</Avatar>
		 }
		  </TableCell>
        {Object.keys(collection[thisCollection].field).map((theField, index)=>{
        let dataField=collection[thisCollection].field[theField]
        if (dataField.table===true){
          return (
            <TableCell conponent='th'  size="medium" key={theField+index}>
				{dataField.type==="boolean"?JSON.stringify(row[theField]):theField==='birthday'?getAge(row[theField]):row[theField]} 
			</TableCell>
          )
        }
      })}
      </TableRow>
      ))}
      </TableBody>
      </Table>		
	   <Grid container spacing={1} alignItems="flex-end">
          <Grid item>
          &nbsp; &nbsp; {t("Items per Page")} :
          <select onChange={handlePageSizeChange} value={pageSize}>
            {pageSizes.map((size) => (
              <option key={size} value={size}>
                {size}
              </option>
            ))}
          </select>
		</Grid>
          <Grid item>
          <Pagination
            className="my-3"
            count={count}
            page={page}
            siblingCount={1}
            boundaryCount={1}
            variant="outlined"
            shape="rounded"
            onChange={handlePageChange}
          />
        </Grid>
		</Grid>
        </Grid>
        </Paper>
      
      <div >
        {currentTutorial && (
          <div>
          <Dialog open={open} onClose={handleClose} fullWidth
              maxWidth="xs" aria-labelledby="form-dialog-title">
			   <Grid container justify='center' style={{marginTop: theme.spacing(2)}}>
					<Avatar style={{ backgroundColor: blue[800], color: blue[100], width: theme.spacing(7), height: theme.spacing(7) }}>
						<Typography variant='h4'>{collection[thisCollection].icon}</Typography>
					</Avatar>
			  </Grid>
              <DialogContent>
                <DialogContentText>
                  {ShowNest(currentTutorial, setCurrentTutorial, thisCollection, true, t)}
                </DialogContentText>
              </DialogContent>
              <DialogActions>
			  
		<Button href={"/add/:"+thisCollection}  color="primary">
					Add
                </Button>
                         
                <Button onClick={handleClose} color="primary">
                  Close Dialog
                </Button>
            </DialogActions>
            
            
          </Dialog>
          </div>


          ) 
        }
      </div>
	     <div>
      <Paper
		className={classes.paper}	   
        id="mouse-over-popover"
        className={classes.popover}

        open={popOpen}
        onClose={handlePopoverClose}
      >
	     {currentTutorial&&
		   <Grid container justify='center' style={{marginTop: theme.spacing(2)}}>
	   { currentTutorial['birthday']?
	   
	   	<Grid container spacing={2} alignItems="flex-end" justify="center">
		<Grid item>
			<Avatar>
			  { getAge(currentTutorial['birthday'])<=15 &&(currentTutorial["sex"]==='1'?"\u{1f466}":"\u{1f467}")}
			  { (getAge(currentTutorial['birthday']) >15 && getAge(currentTutorial['birthday']) <=60 )&&( currentTutorial["sex"]==='1'?"\u{1f9d1}":"\u{1f469}")}
			  { (getAge(currentTutorial['birthday']) >60 ) && (currentTutorial["sex"]==='1'?"\u{1f474}":"\u{1f475}")}
			</Avatar>
		</Grid>
		<Grid item>
			{currentTutorial.fname&&
			<Typography>{currentTutorial.fname} &nbsp; {currentTutorial.lname}</Typography>}
		</Grid>
		</Grid>
		:
	     <Avatar style={{width: theme.spacing(7), height: theme.spacing(7), backgroundColor: blue[100] }}>
		<Typography variant="h4" style={{color: blue[800]}}>
		  {collection[thisCollection].icon&&collection[thisCollection].icon}
		  </Typography>
        </Avatar>		
	   }
			</Grid>
      }
			<div>
				{currentTutorial&& ShowPopover(currentTutorial, thisCollection, t)}
			</div>
      </Paper>
    </div>
    </Grid>
  );
};

export default TutorialsList;
