import React, {useState, useEffect} from 'react'
import { Avatar, Button, CardHeader, Grid, InputAdornment, TextField, Typography} from "@material-ui/core";
import collection from 'crud/collections'
import TutorialDataService from 'services/TutorialService'

const ShowPopover = (currentTutorial, thisCollection, t) =>{  
	return (
	 <div>
	 	<Grid item xs={12} sm={12} >
		{Object.keys(collection[thisCollection].field).map((k, index)=>{
			let dataField=collection[thisCollection].field[k]
			  if (dataField.edit===true){
			   return (					  
				<Grid container spacing={1} alignItems="flex-end">
				<Grid item>
					<Avatar> {dataField.icon}</Avatar>
				</Grid>
				<Grid item xs={9} sm={9}>
				  <TextField id={k+index} 
				    label={t(dataField.show)} 
					fullWidth 
					value={currentTutorial[k]?currentTutorial[k]:''} 
					variant="outlined"
					multiline={currentTutorial[k]&&currentTutorial[k].length>20?true:false}
				  />
				</Grid>
			  </Grid>

			)  }
		}
		)
		}
		</Grid>
		</div>
	)
}
export default ShowPopover